import {
  SEARCH_STOCK,
  REQUEST_STOCK,
  RECEIVE_STOCK,
  RECEIVE_PROFILE,
  ADD_STOCK,
  RECEIVE_TWITTER

} from './constants'
import OAuth2 from 'fetch-mw-oauth2'

export function searchStock(symbol) {
  console.log(symbol);
  return {
    type: SEARCH_STOCK,
    symbol
  }
}

function requestStock(symbol) {
  return {
    type: REQUEST_STOCK,
    symbol
  }
}

function receiveStock(symbol, data) {
  return {
    type: RECEIVE_STOCK,
    symbol,
    data: data
  }
}

function receiveProfile(data) {
  console.log(data);
  return {
    type: RECEIVE_PROFILE,
    description: data.description,
    name: data.name
  }
}

function receiveTwitter(data) {
  console.log(data);
  return {
    type: RECEIVE_TWITTER,
    twitter: data.twitter
  }
}

export function addStock(symbol) {
  return {
    type: ADD_STOCK,
    symbol
  }
}

const finUrl = 'https://finnhub.io/api/v1';
const candleEndpoint = '/stock/candle?';
const profileEndpoint = '/stock/profile?';
const twitterUrl = 'https://api.twitter.com/1.1/search/tweets.json?q=';
const token = "&token=boamq6vrh5rbii6a3j30";

function getFinHubUrl(
  endpoint=candleEndpoint,
  symbol="TWTR",
  count=200,
  resolution="D")
{
  var url = finUrl + endpoint;
  if (endpoint === candleEndpoint) {
    url += "symbol=" + symbol + "&resolution=" + resolution +  "&count=" + count;
  } else if (endpoint === profileEndpoint) {
    url += "symbol=" + symbol;
  }
  url += token;
  return (url);
}

function getTwitterUrl(symbol="TWTR")
{
  return twitterUrl + symbol;
}

function fetchProfile(symbol) {
  return function (dispatch) {
    var url = getFinHubUrl(profileEndpoint, symbol);
    console.log(url);
    return fetch(url)
      .then(
        response => response.json(),
        error => console.log("ERROR: ", error)
      )
      .then(data => dispatch(receiveProfile(data)));
  }
}

function getAuthToken() {
  console.log("attempting to get auth token");
  const oauth2 = new OAuth2({
    grantType: 'client_credentials',
    clientId:'1219744890935865345-f3BU3XYXwfJlQCnZvCW1BNNvwQwVtU',
    clientSecret:'z9720E3QrGhbRnBgfj38wBUpNMFaYraFrjfJCZjGrGEOx',
    tokenEndpoint:"https://api.twitter.com/oauth2/token"
  })
  console.log(oauth2);
  return oauth2;
}

function fetchTwitter(symbol) {
  return function(dispatch) {
    var url = getTwitterUrl(symbol);
    console.log(url);
    const oauth2 = getAuthToken();
    return oauth2.fetch(url)
      .then(
        response => response.json(),
        error => console.log("ERROR: ", error)
      )
      .then(data => dispatch(receiveTwitter(data)));
  }
}

export function fetchStock(symbol) {
  return function(dispatch) {
    dispatch(requestStock(symbol));
    fetchProfile(symbol)(dispatch);
    fetchTwitter("TWTR")(dispatch);
    var url = getFinHubUrl(profileEndpoint, symbol);
    console.log(url);
    return fetch(url)
      .then(
        response => response.json(),
        error => console.log("ERROR: ", error)
      )
      .then(data => dispatch(receiveStock(symbol, data)));
  }
}